	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Day of Resignation","date":"23 Sep 2002","slug":"/2002-09-23-day-of-resignation/","tags":"Y2002"},"html":"<p>It's the day after, and <a href=\"http://www.spiegel.de/\">Spiegel Online</a> is full of resignations. Möllemann resigns from his national FDP party offices (but not as chairman in NRW). Däubler-Gmelin announces she won't be available for the new cabinet (as if Schröder was going to ask). And Merz is making way for Merkel to be Bundestag minority leader. The teams might be the same, but you're going to have to buy a new program to tell the players.</p>"},"next":{"fields":{"title":"Pre-Game Show","date":"22 Sep 2002","slug":"/2002-09-22-pre-game-show/"}},"previous":{"fields":{"title":"Schill Party: 0.6%","date":"23 Sep 2002","slug":"/2002-09-23-schill-party-06/"}}},{"node":{"fields":{"title":"Pre-Game Show","date":"22 Sep 2002","slug":"/2002-09-22-pre-game-show/","tags":"Y2002"},"html":"<p>17:15, and the pre-election shows are already running on TV. The pollsters are hoping that their first projections are as accurate as last year, when they were within 0.13% of the actual results for each party.</p>\n<p>Christopher was asleep, so he and I stayed home when Mama when to the fire station to vote. Our neighbor Achim was in charge there, and reported a record turnout (78% at 16:00). And yes, she voted differently than I would have.</p>"},"next":{"fields":{"title":"Misdiagnosis","date":"22 Sep 2002","slug":"/2002-09-22-misdiagnosis/"}},"previous":{"fields":{"title":"Day of Resignation","date":"23 Sep 2002","slug":"/2002-09-23-day-of-resignation/"}}},{"node":{"fields":{"title":"Misdiagnosis","date":"22 Sep 2002","slug":"/2002-09-22-misdiagnosis/","tags":"Y2002"},"html":"<p>Last night, Christopher complained about his ear again and couldn't sleep. We began to suspect that he actually didn't fall the other night, but instead has an ear infection. Mama took him to the emergency room in Buchholz, and sure enough, an ear infection it is. It was tough to notice because he is in such a good mood, and has no fever. The doctor put him on the 10-day antibiotics / paracetomol / mucus dissolver trip. Now we just have to convince him to settle down for a few days.</p>"},"next":{"fields":{"title":"How I Would Vote","date":"22 Sep 2002","slug":"/2002-09-22-how-i-would-vote/"}},"previous":{"fields":{"title":"Pre-Game Show","date":"22 Sep 2002","slug":"/2002-09-22-pre-game-show/"}}}],"pathPrefix":"","first":false,"last":false,"index":841,"pageCount":1101}}

